.table-wrapper{overflow-y:auto}table{width:100%;min-width:400px;border-spacing:0}tr{text-align:left}th,td{text-transform:capitalize;padding:15px 10px}.table__pagination{display:flex;width:100%;justify-content:flex-end;align-items:center;margin-top:10px}.table__pagination-item{width:30px;height:30px;border-radius:20px;display:flex;align-items:center;justify-content:center;cursor:pointer}.table__pagination-item~.table__pagination-item{margin-left:10px}.table__pagination-item:hover{color:var(--txt-white);background-color:var(--second-color)}.table__pagination-item.active,.table__pagination-item.active:hover{background-color:var(--main-color);color:var(--txt-white);font-weight:600}.status{border-radius:10px;color:#fff;display:inline-block;margin:5px;padding:10px 24px;width:180px;font-size:15px;text-align:center}.status.green{background-color:#19cf41}.status.red{background-color:#eb1421}.icon{font-size:22px;margin-right:10px}.checkBoxStatus{-webkit-appearance:none;appearance:none;width:170px;padding-left:40px;margin:0;border-radius:16px;background:radial-gradient(circle 12px, rgb(96, 95, 95) 100%, transparent calc(100% + 1px)) #ccc -65px;transition:.3s ease-in-out}.checkBoxStatus::before{content:"Ngưng hoạt động";font:bold 12px/32px Verdana;color:#fff}:checked{padding-left:8px;background-color:#19cf41;background-position:65px}:checked::before{content:"Đang hoạt động"}